import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { BuySellComponent } from './core-module/buy-sell/buy-sell.component';
import { BuyComponent } from './core-module/buy-sell/types/buy/buy.component';
import { SellComponent } from './core-module/buy-sell/types/sell/sell.component';
import { DashboardComponent } from './core-module/dashboard/dashboard.component';
import { LoginComponent } from './core-module/auth/login/login.component';
import { RegisterComponent } from './core-module/auth/register/register.component';
import { WalletComponent } from './core-module/wallet/wallet.component';
import { SinglewalletComponent } from './core-module/wallet/singlewallet/singlewallet.component';
import { AccountComponent } from './core-module/account/account.component';
import { TermsComponent } from './core-module/includes/terms/terms.component';
import { DepositComponent } from './core-module/deposit/deposit.component';
import { WithdrawComponent } from './core-module/withdraw/withdraw.component';
import { DepositWithdrawComponent } from './core-module/deposit-withdraw/deposit-withdraw.component';
const routes: Routes = [
  { path: '', redirectTo: 'dashboard', pathMatch: 'full' },
  { path: 'dashboard', component: DashboardComponent},
  { path: 'buysell', component: BuySellComponent },
  { path: 'buy', component: BuyComponent },
  { path: 'sell', component: SellComponent },
  { path: 'wallet', component: WalletComponent },
  { path: 'singlewallet', component: SinglewalletComponent },
  { path: 'sell', component: SellComponent },
  { path: 'login', component: LoginComponent},
  { path: 'register', component: RegisterComponent},
  { path: 'account', component: AccountComponent},
  { path: 'terms', component: TermsComponent},
  { path: 'deposit_money', component: DepositComponent},
  { path: 'withdraw_money', component: WithdrawComponent},
  { path: 'fiat_wallet', component: DepositWithdrawComponent}

];

@NgModule({
  imports: [RouterModule.forRoot(routes, { useHash: true })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
