import { Component, OnInit } from '@angular/core';
import { ApiServiceService } from '../../api-service.service';
import { AppComponent } from '../../app.component';
import { Router, ActivatedRoute } from '@angular/router';
import { async } from '@angular/core/testing';
@Component({
  selector: 'app-account',
  templateUrl: './account.component.html',
  styleUrls: ['./account.component.css']
})
export class AccountComponent implements OnInit {

  constructor(private app_component: AppComponent, private _router: Router, private _ApiServiceService: ApiServiceService) { }
  firstName: any;
  lastName: any;
  email: any;
  mobile_number: any;
  address: any;
  postal: any;
  dob: any;
  address_proof: any;
  id_proof: any;
  photo_proof: any;
  public base64textString: any = [];
  dummyImage: any = 'https://www.narda-sts.com/fileadmin/_processed_/csm_no-image-available_EN_3dd8d65e1e.png';
  addressProof: any = this.dummyImage;
  IdProof: any = this.dummyImage;
  PhotoProof: any =this.dummyImage;

  ngOnInit() {
    this.getAccountDetails();
  }
  public user: any = [];
  getAccountDetails() {
    this._ApiServiceService.getUserDetails().subscribe((response) => {
      var dataItem = JSON.parse(response['_body']);
      // console.log(dataItem);
      this.firstName = dataItem.firstName;
      this.lastName = dataItem.lastName;
      this.email = dataItem.email;
      this.dob = dataItem.dob;
      this.mobile_number = dataItem.mobile_number;
      this.address = dataItem.address;
      this.postal = dataItem.postal;

      if (dataItem.proofs!=null) {
        this.addressProof = dataItem.proofs.addressProof ? dataItem.proofs.addressProof : this.dummyImage;
        this.IdProof = dataItem.proofs.idProof ? dataItem.proofs.idProof : this.dummyImage;
        this.PhotoProof = dataItem.proofs.photoProof ? dataItem.proofs.photoProof : this.dummyImage;
      }

      // console.log(this.addressProof);
      // console.log(this.IdProof);
      // console.log(this.PhotoProof);

    });
  }

  updateAccountInfo() {

    this._ApiServiceService.updateUserDetails(this.dob, this.firstName, this.lastName, this.email, this.mobile_number, this.address, this.postal).subscribe((response) => {
      var dataItem = JSON.parse(response['_body']);
      this.user = dataItem;
      this.app_component.open(dataItem.message);
      console.log(dataItem);
    });
  }
  file: any;
  type: any;
  uploadKyc(event: EventTarget, type) {
    var image;
    this.type = type;
    let eventObj: MSInputMethodContext = <MSInputMethodContext>event;
    let target: HTMLInputElement = <HTMLInputElement>eventObj.target;
    var files: FileList = target.files;
    this.file = files[0];
     // get file extensions
    
     var fileName=this.file.name;
     var validFormats = ['jpg','jpeg','png'];
     var ext= fileName.split('.')[1];
 
     // check and Validate file type.
 
     if(validFormats.indexOf(ext) == -1){
         this.app_component.open('Trying to upload unsupported file type');
     }else{
         document.getElementById(type+'_icon').style.display='block';
        
         var reader = new FileReader();
         reader.onload = this.handleReaderLoaded.bind(this);
         reader.readAsBinaryString(this.file);
     }


  }
  handleReaderLoaded = async (e) => {
    // }
    this.base64textString.push('data:image/png;base64,' + btoa(e.target.result));

    console.log('type', this.type);
    var image = this.base64textString[0];
    var text = image.split(',')[1];
    this._ApiServiceService.uploadImage(text).subscribe((response) => {
      // console.log(response);
      var dataItem = JSON.parse(response['_body']);
      console.log(dataItem.secure_url);
      this._ApiServiceService.uploadproof(this.type, dataItem.secure_url).subscribe((response) => {
        console.log(response);
         document.getElementById(this.type+'_icon').style.display='none';
        this.base64textString.length = 0;
        this.getAccountDetails();
        this.app_component.open('Kyc Updated Succesfuly');
        var dataItem = JSON.parse(response['_body']);
        console.log(dataItem);
      });
    });


  }



}
