import { Component, OnInit, ViewChild } from '@angular/core';
import { ApiServiceService } from '../../../../api-service.service';
import { MatSnackBarModule } from '@angular/material/snack-bar'
import { Router, ActivatedRoute } from '@angular/router';
import { AppComponent } from '../../../../app.component';
@Component({
  selector: 'app-sell',
  templateUrl: './sell.component.html',
  styleUrls: ['./sell.component.css']
})
export class SellComponent implements OnInit {

  constructor(private route: ActivatedRoute, private app_component: AppComponent, private _router: Router, private _ApiServiceService: ApiServiceService, private snackBar: MatSnackBarModule) { }

  currentPrice: any;
  sellamount: any;
  value_updated: any;
  str_value: any;
  btn_name: any = "Sell";
  transactionFee: any;
  total_amount: any;
  is_login: any;
  coin_name: any;
  sub: any;
  available_balance:any=0;
  transactionFeePercentage: any;

  // sell

  
  //withdraw 
withdraw_amount:any;
routingNumber:any;
accountHolder:any;
phoneNumber:any;
accountNumber:any;
state:any;
postalCode:any;
line1:any;
city:any;
dob_month:any;
dob_year:any;
dob_day:any;
ssn:any;
lastName:any;
firstName:any;
file:any;
base64textString:any=[];
verificationFile:any;
currency:any='usd';
saveDetails:any=false;
show_fill_details:any;
waitingForResponse:any=false;
coin_balance:any;
is_have_account:any;
savedAccount:any;
savedData:any={};
is_select:any=false;
  ngOnInit() {
    this.show_fill_details=false;
    this.routingNumber = '110000000';
    this.accountNumber = '000123456789';
    this.accountHolder = 'Jenny';
    this.postalCode = '10001';
    this.phoneNumber=9600975087;
    this.ssn='0987';
    console.log(this.routingNumber);

    var user_id = localStorage.getItem('user_id');
    if (user_id == undefined) {
      this.is_login = false;
      this.btn_name = "Login to Sell";
    } else {
      this.is_login = true;


    }
    this.sub = this.route
      .queryParams
      .subscribe(params => {
        if (params.query != undefined) {
          this.coin_name = params.query;
        }
        // //console.log(params.type);
      });

    this._ApiServiceService.getCoinPrice('XLM', 'AUD').subscribe((response) => {
      var dataItem = JSON.parse(response['_body'])
      this.currentPrice = dataItem.AUD;
      this.value_updated = true;
      this.getSingleWalletDetails();
    });
   //getSavedAccount
   this._ApiServiceService.getSavedAccount().subscribe((response) => {
    var dataItem = JSON.parse(response['_body'])
    console.log("savedAccount", dataItem);
    this.is_have_account = 0;
    if (dataItem != "") {
      this.is_have_account = 1;
      this.savedAccount = dataItem;
      for (let index = 0; index < this.savedAccount.length; index++) {
        this.savedAccount[index].selected = false;

      }
    }

  });

  }

  selectedWithdrawBankDetails(bankData) {
    this.savedAccount.forEach(element => {
      element.selected = false;
    });

    bankData.selected = true;
    console.log(bankData);
    this.savedData = bankData;
    this.is_select=true;
  }
  SellCoinBySelectedAccount(){
    if(this.is_select==false){
      this.app_component.open("Select any saved Account.");
    }else{
    var actual_usd = this.currentPrice * this.sellamount;
    this.savedData.total_amount=this.total_amount;
    this.savedData.amount= this.total_amount.toFixed(6);
    this.savedData.withdraw_amount= actual_usd;
    this.savedData.coin_name= this.coin_name;
    this.savedData.sellamount =this.sellamount;
    this.savedData.currency =this.currency;
    console.log(this.savedData);
    this._ApiServiceService.sellCoinBysavedAccount(this.savedData).subscribe((response) => {
      var dataItem = JSON.parse(response['_body']);
      console.log("completed",dataItem);
      console.log("status",dataItem.status);
      
      if (dataItem.status == true) {
        this.btn_name = "Sell";
       
        this.showConfirmModel.hide();
        this.Completed.show();
        this.app_component.open("Coin Sold sucussfully");
      } else {
        this.btn_name = "Sell";
        this.app_component.open("Try again, oops! Something went wrong..");
      }

    });
  }


    // this.total_amount,this.sellamount,this.coin_name,this.transactionFee,
  }
  showFillDetails (){
    if(this.sellamount>this.coin_balance){ //1 > 0
      this.app_component.open("Insufficient balance to Sell");
    }else if (this.sellamount == undefined) {
      this.app_component.open("Please Enter To Amount");
    } else {
      this.show_fill_details=true;
    }

  }

  gotoBuy(id_val,name){
    this._router.navigate(['/buy'], { queryParams: { query: this.coin_name } });
  }
  gotoSingleWalletPage(type) {
    this._router.navigate(['/singlewallet'], { queryParams: { name: this.coin_name ,type:type  } });
  }
  priceLiveUpate() {
    this.value_updated = false;
    this._ApiServiceService.getCoinPrice('XLM', 'AUD').subscribe((response) => {
      var dataItem = JSON.parse(response['_body'])
    var AUD= dataItem.AUD;
      this.value_updated = true;

      var admin;
      this._ApiServiceService.getAdmin().subscribe((response) => {
        var dataItem = JSON.parse(response['_body'])
        admin = dataItem;
        this.transactionFeePercentage = admin.sellTransactionFee;
        this.transactionFee = (this.sellamount * admin.sellTransactionFee) / 100;
        this.total_amount = parseFloat(this.sellamount) - this.transactionFee;
        this.str_value = (AUD * this.total_amount).toFixed(4);
      })
    });
  }
  sellCoin() {

    if(this.is_select==true){
      this.btn_name = "Processing.. Please Wait";
      this.SellCoinBySelectedAccount();
    }else{
    var user_id = localStorage.getItem('user_id');
    if (user_id == undefined) {
      this._router.navigate(['login']);
    } else {
      this.btn_name = "Processing.. Please Wait";
      //console.log("here");
      var actual_usd = this.currentPrice * this.sellamount;
      this._ApiServiceService.sellStellar(this.total_amount,this.sellamount,this.coin_name,this.transactionFee,this.verificationFile,actual_usd,this.routingNumber,this.accountHolder,this.phoneNumber,this.accountNumber,this.state,this.postalCode,this.line1,this.city,this.dob_month,this.dob_year,this.dob_day,this.ssn,this.lastName,this.firstName,this.currency,this.saveDetails,this.savedData).subscribe((response) => {
        var dataItem = JSON.parse(response['_body']);
        if (dataItem.status == true) {
              this.btn_name = "Sell";
             
              this.showConfirmModel.hide();
              this.Completed.show();
              this.app_component.open("Coin Sold sucussfully");
            } else {
              this.btn_name = "Sell";
              this.app_component.open("Try again, oops! Something went wrong..");
            }
      });
    }
  }
  }

  showModel() {
    if (this.is_select == false) {
      this.app_component.open("Please Select any saved Account.");
    } else {
      this.showConfirmModel.show();
    }

  }

  closeModel() {
    this.showConfirmModel.hide();
    this.Completed.hide();


  }
  getSingleWalletDetails() {
    this._ApiServiceService.getSingleWalletPriceByCoinName(this.coin_name).subscribe((response) => {
      var dataItem = JSON.parse(response['_body'])
      if (dataItem.balance) {
        this.coin_balance=dataItem.balance;
        this.available_balance = (dataItem.balance * this.currentPrice).toFixed(2);
      }
    });
  }


  confirm_sell(){
     if(this.firstName==undefined){
      this.app_component.open("Please Enter firstName");
    }else if(this.lastName==undefined){
      this.app_component.open("Please Enter lastName");
    }else if(this.ssn==undefined){
      this.app_component.open("Please Enter ssn");
    }else if(this.accountHolder==undefined){
      this.app_component.open("Please Enter accountHolder");
    }else if(this.accountNumber==undefined){
      this.app_component.open("Please Enter accountNumber");
    }else if(this.phoneNumber==undefined){
      this.app_component.open("Please Enter phoneNumber");
    }else if(this.dob_day==undefined){
      this.app_component.open("Please Enter Day");
    }else if(this.dob_month==undefined){
      this.app_component.open("Please Enter Month");
    }else if(this.dob_year==undefined){
      this.app_component.open("Please Enter Year");
    }else if(this.state==undefined){
      this.app_component.open("Please Enter State");
    }else if(this.city==undefined){
      this.app_component.open("Please Enter City");
    }else if(this.postalCode==undefined){
      this.app_component.open("Please Enter postalCode");
    }else if(this.line1==undefined){
      this.app_component.open("Please Enter Address");
    }else if(this.verificationFile==undefined){
      this.app_component.open("Please Upload Verification File")
    }else {

      this.showConfirmModel.show();
     

    }

  }


  
  uploadKyc(event: EventTarget) {
    
    var image;
    let eventObj: MSInputMethodContext = <MSInputMethodContext>event;
    let target: HTMLInputElement = <HTMLInputElement>eventObj.target;
    var files: FileList = target.files;
    this.file = files[0];

    // get file extensions
    
    var fileName=this.file.name;
    var validFormats = ['jpg','jpeg','png'];
    var ext= fileName.split('.')[1];

    // check and Validate file type.

    if(validFormats.indexOf(ext) == -1){
        this.app_component.open('Trying to upload unsupported file type');
    }else{
        document.getElementById('btn_withdraw').style.display='none';
        document.getElementById('image_load').style.display='block';
        document.getElementById('image_load').style.marginLeft='155px';
        var reader = new FileReader();
        reader.onload = this.handleReaderLoaded.bind(this);
        reader.readAsBinaryString(this.file);
    }
  }
  handleReaderLoaded = async (e) => {
    this.base64textString.push('data:image/png;base64,' + btoa(e.target.result));
    var image = this.base64textString[0];
    var text = image.split(',')[1];
    this._ApiServiceService.uploadImage(text).subscribe((response) => {
      var dataItem = JSON.parse(response['_body']);
      this.verificationFile=dataItem.url;
      document.getElementById('btn_withdraw').style.display='block';
      document.getElementById('image_load').style.display='none';
    });

  }
  @ViewChild('showConfirmModel') showConfirmModel;
  @ViewChild('Completed') Completed;
}
