import { Component, OnInit } from '@angular/core';
import { ApiServiceService } from '../../../api-service.service';
import { MatSnackBarModule } from '@angular/material/snack-bar'
import { Router, ActivatedRoute } from '@angular/router';
import { AppComponent } from '../../../app.component';
@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {

  constructor(private app_component: AppComponent,private _router: Router, private _ApiServiceService: ApiServiceService, private snackBar: MatSnackBarModule) { }
  public password: any;
  public email: any;
  public repassword: any; 
  public btn_name: any="Create Account"; 
  ngOnInit() {
   
    var email_id= localStorage.getItem('reg_email');
    if(email_id!=undefined){
      this.email=email_id.replace(/['"]+/g, '');
      // this.email=email_id;
    }

  }

  hitregister() {
 
    console.log(this.repassword);
    if(this.email==undefined){
      this.app_component.open("Please enter Email Address");
    }else if(this.password!=this.repassword || this.repassword ==undefined){
      this.app_component.open("Password is not match");
    }else{
      this.btn_name="processing..";
    this._ApiServiceService.userRegister(this.email,this.password).subscribe((response) => {
    
      var dataItem = JSON.parse(response['_body'])
      console.log(dataItem);
      if (dataItem.message =="Existing user"){
        this.app_component.open("Email is already Exists");
      }else{
        this.app_component.open("Thank you for Register with us");
      localStorage.setItem('user_id', JSON.stringify(dataItem.response._id));
      localStorage.setItem('stellar_address', JSON.stringify(dataItem.response.stellarAddress));
      console.log(dataItem);
      this._router.navigate(['dashboard']);
      }
    });
    }
    
  }


}
