import { Component, OnInit } from '@angular/core';
import { ApiServiceService } from '../../api-service.service';
import { MatSnackBarModule } from '@angular/material/snack-bar'
import { Router, ActivatedRoute } from '@angular/router';
import { AppComponent } from '../../app.component';
@Component({
  selector: 'app-deposit-withdraw',
  templateUrl: './deposit-withdraw.component.html',
  styleUrls: ['./deposit-withdraw.component.css']
})
export class DepositWithdrawComponent implements OnInit {

  constructor(private app_component: AppComponent, private _router: Router, private _ApiServiceService: ApiServiceService, private snackBar: MatSnackBarModule) { }
  deposits:any=[];
  wallets:any;
  total_amount:any;
  ngOnInit() {
    var user_id = localStorage.getItem('user_id');
    if (user_id != undefined) {
    this.getSingleWalletDetails();
    this.getFiatWalletAmont();
    }

  }


  getSingleWalletDetails() {
    this._ApiServiceService.depositTransaction().subscribe((response) => {
      var dataItem = JSON.parse(response['_body'])
      console.log(dataItem);
      this.deposits = dataItem;
      // //console.log(this.singleWallet.coin_name)
    });
  }

  getFiatWalletAmont(){
    this._ApiServiceService.getFiatWallet().subscribe((response) => {
      var dataItem = JSON.parse(response['_body'])
      this.wallets=dataItem;
      if(this.wallets.length!=0){
      this.total_amount=dataItem[0].balance!=undefined?dataItem[0].balance:0;
    }
      console.log(this.wallets);
    });
  }
}
