import { Component, OnInit, ViewChild } from '@angular/core';
import { ApiServiceService } from '../../../api-service.service';
import { MatSnackBarModule } from '@angular/material/snack-bar'
import { Router, ActivatedRoute } from '@angular/router';
import { AppComponent } from '../../../app.component';

@Component({
  selector: 'app-singlewallet',
  templateUrl: './singlewallet.component.html',
  styleUrls: ['./singlewallet.component.css']
})
export class SinglewalletComponent implements OnInit {

  constructor(private route: ActivatedRoute, private app_component: AppComponent, private _router: Router, private _ApiServiceService: ApiServiceService, private snackBar: MatSnackBarModule) { }
  sub: any;
  id: any;
  stellar_address: any;
  currentPrice: any;
  sellamount: any;
  value_updated: any;
  str_value: any;
  btn_name: any = "Send";
  transactionFee: any = 0;
  total_amount: any;
  is_login: any;
  toAddress: any;
  transactionFeePercentage: any;
  menu: any = 'wallet';
  approx_usd: any = 0;
  coin_name: any;
  ngOnInit() {
    this.sub = this.route
      .queryParams
      .subscribe(params => {
        this.coin_name = params.name;
      });

    this.sub = this.route
      .queryParams
      .subscribe(params => {
        if (params.type != undefined) {
          this.menu = params.type;
        }
        // //console.log(params.type);
      });
    this.stellar_address = localStorage.getItem('stellar_address');

    this.getTransaction();
    this._ApiServiceService.getCoinPrice('XLM', 'AUD').subscribe((response) => {
      var dataItem = JSON.parse(response['_body'])
      //console.log(dataItem.AUD);
      // this.sellamount = 1
      this.currentPrice = dataItem.AUD;
      this.value_updated = true;
      this.getSingleWalletDetails();
    });



  }

  // getSingleWalletDetails() {
  //   this._ApiServiceService.singleWallet(this.id).subscribe((response) => {
  //     var dataItem = JSON.parse(response['_body'])
  //     //console.log(dataItem);
  //     this.singleWallet = dataItem;
  //     //console.log(this.singleWallet.coin_name)
  //     this.approx_usd= this.currentPrice * this.singleWallet.balance;
  //   });
  // }
  public singleWallet: any = [];
  getSingleWalletDetails() {
    this._ApiServiceService.getSingleWalletPriceByCoinName(this.coin_name).subscribe((response) => {
      if (response['_body']) {
        var dataItem = JSON.parse(response['_body']);
        this.singleWallet = dataItem;
        console.log(this.singleWallet);
        if(this.singleWallet.balance!=undefined){
          this.approx_usd = (this.singleWallet.balance * this.currentPrice).toFixed(2);
        }
      }
    });
  }



  show_menu(menu_name) {
    this.menu = menu_name;
    //console.log(this.menu);
  }
  public transactions: any = [];
  getTransaction() {
    this._ApiServiceService.getTransactions().subscribe((response) => {
      var dataItem = JSON.parse(response['_body'])
      //console.log(dataItem);
      this.transactions = dataItem;
      //console.log(this.singleWallet.coin_name)
    });
  }

  priceLiveUpate() {
    this.value_updated = false;
    this._ApiServiceService.getCoinPrice('AUD', 'XLM').subscribe((response) => {
      var dataItem = JSON.parse(response['_body'])
      // //console.log(dataItem.XLM);
      this.str_value = (dataItem.XLM * this.sellamount).toFixed(4);
      this.value_updated = true;

      var admin;
      this._ApiServiceService.getAdmin().subscribe((response) => {
        var dataItem = JSON.parse(response['_body'])
        admin = dataItem;
        //console.log(admin);
        this.transactionFeePercentage = admin.sellTransactionFee;
        //console.log(admin.sellTransactionFee);
        //console.log(this.sellamount);
        //console.log("--------------");
        this.transactionFee = (this.sellamount * admin.sellTransactionFee) / 100;
        this.total_amount = parseFloat(this.sellamount) - this.transactionFee;
        //console.log(this.transactionFee);
        //console.log(this.total_amount);
        //console.log(this.sellamount);

      })
    });
  }
  sellCoin() {
    var user_id = localStorage.getItem('user_id');
    if (user_id == undefined) {
      this._router.navigate(['login']);
    } else {
      //console.log("sellCoin hitted");
      this.btn_name = "Processing.. Please Wait";
      this._ApiServiceService.SendStellar(this.total_amount, this.sellamount, this.transactionFee, this.toAddress).subscribe((response) => {
        //console.log('result', response);
        var dataItem = JSON.parse(response['_body'])
        //console.log(dataItem);
        if (dataItem.status == true) {
          this.btn_name = "Send";
          this.str_value = 0;
          this.sellamount = 0;
          this.showConfirmModel.hide();
          this.Completed.show();
          // alert("Sold sucussfully");
        } else {
          this.btn_name = "Send";
          this.app_component.open(dataItem.message);
        }

      });
    }
  }

  showModel() {
    if (this.sellamount == undefined) {
      this.app_component.open("Please Enter To Amount");
    } else if (this.toAddress == undefined) {
      this.app_component.open("Please Enter To address");
    } else if (this.sellamount > this.singleWallet.balance) {
      this.app_component.open("Insufficient balance to Send");
    } else {
      this.showConfirmModel.show();
    }

  }

  closeModel() {
    this.showConfirmModel.hide();
    this.Completed.hide();


  }



  @ViewChild('showConfirmModel') showConfirmModel;
  @ViewChild('Completed') Completed;
}