import { Component, OnInit } from '@angular/core';
import { ApiServiceService } from '../../api-service.service';
import { MatSnackBarModule } from '@angular/material/snack-bar'
import { Router, ActivatedRoute } from '@angular/router';
import { AppComponent } from '../../app.component';

declare var jquery:any;
declare var $ :any;

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {

  constructor(private app_component: AppComponent,private _router: Router, private _ApiServiceService: ApiServiceService, private snackBar: MatSnackBarModule) { }
  public password: any;
  public email: any;
  public repassword: any; 
public is_login :any;
public reg_email :any
public wallets  :any;
public is_loading =true;
public  approx_usd :any;
public current_price :any;
public total_usd :any="$ 0";
public stellarBalance:any=0;
  ngOnInit() {
    var user_id=localStorage.getItem('user_id');
    if(user_id==undefined){
      this.is_login=false;
    }else{
      this.is_login=true;

      this._ApiServiceService.getUserWallet().subscribe((response) => {
        var dataItem = JSON.parse(response['_body'])
        this.wallets=dataItem;
        //console.log(dataItem);
      
        this._ApiServiceService.getCoinPrice('XLM', 'AUD').subscribe((response) => {
          var dataItem = JSON.parse(response['_body'])
          //console.log(dataItem.AUD);
          this.approx_usd=dataItem.AUD;
          if(this.wallets.length!=0){
          this.stellarBalance=this.wallets[0].balance;
          for (var i = this.wallets.length - 1; i >= 0; i--) {
            this.wallets[i].current_price = "$ "+ this.approx_usd;
            this.wallets[i].approx_usd = "$ " +this.approx_usd * this.wallets[i].balance;
            this.total_usd = "$ " + this.approx_usd * this.wallets[i].balance;
          }
        }
         
        });
        //console.log("Wallets ",this.wallets);
        
      });

    }

    $(document).ready(function(){
      $('[data-toggle="tooltip"]').tooltip();   
    });
  }

  hitregister() {
    if(this.email==undefined){
      this.app_component.open("Please enter email");
    }
    else if(this.password!=this.repassword){
      this.app_component.open("Password is not match");
    }else{
    this._ApiServiceService.userRegister(this.email,this.password).subscribe((response) => {
    
      var dataItem = JSON.parse(response['_body'])
      //console.log(dataItem);
      if (dataItem.message =="Existing user"){
        this.app_component.open("Email is already Exists");
      }else{
        this.app_component.open("Thank you for Register with us");
      localStorage.setItem('user_id', JSON.stringify(dataItem.response._id));
      localStorage.setItem('stellar_address', JSON.stringify(dataItem.stellarAddress));
      //console.log(dataItem);
      this._router.navigate(['buysell']);
      }
    });
    }
    
  }
gotoRegisterPage(){
localStorage.setItem('reg_email',JSON.stringify(this.reg_email));
this._ApiServiceService.checkUser(this.reg_email).subscribe((response) => {
    
  var dataItem = JSON.parse(response['_body'])
  console.log(dataItem);
  if (dataItem.status ==true){
    this._router.navigate(['login']);
  }else{
    this._router.navigate(['register']);
  }
});

// 
}
}
