  uploadKyc(event: EventTarget) {
    
    var image;
    let eventObj: MSInputMethodContext = <MSInputMethodContext>event;
    let target: HTMLInputElement = <HTMLInputElement>eventObj.target;
    var files: FileList = target.files;
    this.file = files[0];

    // get file extensions

    var fileName=this.file.name;
    var validFormats = ['jpg','jpeg','png'];
    var ext= fileName.split('.')[1];

    // check and Validate file type.

    if(validFormats.indexOf(ext) == -1){
        this.app_component.open('Trying to upload unsupported file type');
    }else{
        document.getElementById('btn_withdraw').style.display='none';
        document.getElementById('image_load').style.display='block';
        document.getElementById('image_load').style.marginLeft='155px';
        var reader = new FileReader();
        reader.onload = this.handleReaderLoaded.bind(this);
        reader.readAsBinaryString(this.file);
    }
  }
  handleReaderLoaded = async (e) => {
    this.base64textString.push('data:image/png;base64,' + btoa(e.target.result));
    var image = this.base64textString[0];
    var text = image.split(',')[1];
    this._ApiServiceService.uploadImage(text).subscribe((response) => {
      var dataItem = JSON.parse(response['_body']);
      this.verificationFile=dataItem.url;
      //console.log(dataItem.url);
      document.getElementById('btn_withdraw').style.display='block';
      document.getElementById('image_load').style.display='none';
    });


  }


