import { Component,ViewEncapsulation,Compiler  } from '@angular/core';
import { ApiServiceService } from './api-service.service';
import { environment } from '../environments/environment';
import {
  MatSnackBar,
  MatSnackBarConfig,
  MatSnackBarHorizontalPosition,
  MatSnackBarVerticalPosition,
} from '@angular/material';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css'],
  encapsulation: ViewEncapsulation.None,
})

export class AppComponent {
  title = 'coins';


  name = 'Angular 4';

  message: string = 'test Bar opened.';
  actionButtonLabel: string = 'Close';
  action: boolean = true;
  setAutoHide: boolean = true;
  autoHide: number = 2000;
  horizontalPosition: MatSnackBarHorizontalPosition = 'center';
  verticalPosition: MatSnackBarVerticalPosition = 'bottom';

  addExtraClass: boolean = false;
  public base_url :any;
  constructor(private service : ApiServiceService,public snackBar: MatSnackBar,private _compiler: Compiler) {
    
  }
  open(snac_message) {
    this.service.getCurrentUserId();
    let config = new MatSnackBarConfig();
    config.verticalPosition = this.verticalPosition;
    config.horizontalPosition = this.horizontalPosition;
    config.duration = this.setAutoHide ? this.autoHide : 0;
    config.panelClass = 'blue-snackbar';
      this.snackBar.open(snac_message, this.action ? this.actionButtonLabel : undefined, config);

  }
 
}
