import { Component, OnInit } from '@angular/core';
import { ApiServiceService} from '../../../api-service.service';
import { AppComponent } from '../../../app.component';
import { Router, ActivatedRoute } from '@angular/router';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  public email:any;
  public password: any="Blockstreet";

  constructor(private app_component: AppComponent,private _router:Router,private _ApiServiceService: ApiServiceService) { }

  ngOnInit() {

    var email_id= localStorage.getItem('reg_email');
    this.email ="user@blockstreet.com";
    if(email_id!=undefined){
      this.email=email_id.replace(/['"]+/g, '');
      // this.email=email_id;
    }
  }
  hitlogin() {
    this._ApiServiceService.userlogin(this.email,this.password).subscribe((response) => {
      var dataItem = JSON.parse(response['_body'])
      //console.log(dataItem);
      if(dataItem.status==true){
      localStorage.setItem('user_id', JSON.stringify(dataItem.user._id));
      localStorage.setItem('stellar_address', JSON.stringify(dataItem.user.stellarAddress));
      // //console.log(dataItem);
      this._router.navigate(['dashboard']);
      this.app_component.open("User logged successfully");
    }else{
      this.app_component.open(dataItem.message);
    }
    });
    
  }
}
